import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ModalController } from 'ionic-angular';



import { ApiProvider } from '../../providers/api/api';
import { LoadingController } from 'ionic-angular';
import { Events } from 'ionic-angular';




/**
 * Generated class for the ListaServiciosPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-lista-servicios',
  templateUrl: 'lista-servicios.html',
})
export class ListaServiciosPage {

  constructor(public navCtrl: NavController, public navParams: NavParams,public modalCtrl: ModalController,public apiProvider: ApiProvider, public loadingController:LoadingController, public events: Events) {

  this.categorias = [];

  }



   presentProfileModal() {
   let profileModal = this.modalCtrl.create('BuscarModalPage');
   profileModal.present();
 }


ionViewWillEnter(){
  
      console.log('ionViewDidLoad ListaServiciosPage');
    this.categoriaSeleccionada =  this.navParams.get('nombre');
    console.log(this.categoriaSeleccionada);

    
}

  ionViewDidLoad() {

      this.section="one";


      this.apiProvider.categoriasActivas()
      .then(data => {
       console.log(data);
       this.categorias = data || [];
      });



  }

      goCentro() {
     this.navCtrl.push('PerfilCentroPage');  
  		}

      getServiciosGPS(){
      console.log('gps');
        navigator.geolocation.getCurrentPosition(function(pos) {
          console.log(pos.coords.latitude+' Long: '+ pos.coords.longitude);
          let latitudePerson = pos.coords.latitude;
          let longitudePerson = pos.coords.longitude;
        }, function(error) {
          console.log('some err');
          console.log(error);
        });
      }


  openBusqueda() {
      this.presentProfileModal();
      }

      




}



